@import url('./normalize.css/normalize.css');

body {
  font-family: 'Courier','TsukuARdGothic-Regular','Hiragino Kaku Gothic ProN','ヒラギノ角ゴ ProN W3','メイリオ', Meiryo,'Yu Gothic UI',YuGothic,'Yu Gothic',sans-serif;
  line-height: 1.6;
  letter-spacing: .05em;
  color:#231815;
  transition: z-index 0ms 5.28455ms;
  background: #fff;
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  transition: .2s;

  &:hover {
    text-decoration: none !important;
    opacity: .7;
  }
}

pre {
  background-color: transparent;
  border: none;
  padding: 16px 0;
}

p {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.pc-none {

  @media (min-width:768px) {
    display: none !important;
  }
}

.sp-none {
  display: none !important;

  @media (min-width:768px) {
    display: block !important;
  }
}
